.AddGame {
  min-height: calc(100vmin);
  background-image: url("https://i.pinimg.com/originals/ac/22/59/ac225995e118a67fd7ce3cecc43fc81a.png");
  background-position: left top;
  background-repeat: no-repeat;
  background-size: 60%;
  border-top: 1px solid black;
}

.AddGame-container {
  width: 35%;
  padding: 8% 10%;
  float: right;
}

.AddGame-checked {
  padding: 20% 0 10% 30%;
}

.AddGame-checked-logo {
}

@media screen and (max-width: 480px) {
  .AddGame {
    background-image: none;
    border-top: 1px solid black;
  }
}

@media screen and (max-width: 480px) {
  .AddGame-container {
    width: 70%;
    padding: 8% 10%;
    text-align: center;
  }
}